'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { notFound } from 'next/navigation.js';
import { CSSProperties, ReactElement, use, useCallback, useEffect, useMemo, useRef } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import { ListRange } from 'react-virtuoso';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { useElementOffsetY } from '@yandex-music-int/sol/common/hooks/useElementOffsetY.js';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { Header } from '@/widgets/content/index.js';
import { PageHeaderAlbum, PageHeaderShimmer } from '@/widgets/page-header/index.js';

import { AlbumsCarousel } from '@/features/album/index.js';
import { TrackAlbum } from '@/features/track/index.js';

import {
    CONTAINER_COOKIE_STORAGE,
    getAverageBackgroundColor,
    LoadingState,
    NotificationContainerId,
    TrackVariant,
    useContainer,
    useRumPage,
    AnalyticsPageProvider,
    useCreateFrom,
    AnalyticsBlock,
    AnalyticsPage,
    useStore,
    useNotify,
    Experiments
} from '@/shared/lib/index.js';
import { Footer, BaseNotificationError, TextShimmer, TrackShimmer, VirtualScroll } from '@/shared/ui/index.js';

import { VolumeType } from '../../lib/types/VolumeType.js';
import { VolumeItemModel } from '../../model/VolumeItem.js';

import styles from './Album.module.scss';

const DEBOUNCE_DURATION_MS = 300;
const DEFAULT_TRACK_SHIMMER_COUNT = 10;

interface Props {
    albumId?: string | number;
}

const HEADER_TO_VIEWPORT_OFFSET_NEW_NAVIGATION = 93;
const HEADER_TO_VIEWPORT_OFFSET_OLD_NAVIGATION = 17;

export const AlbumPage = observer(({ albumId }: Props): ReactElement => {
    const from = useCreateFrom({ pageId: AnalyticsPage.ALBUM, blockId: AnalyticsBlock.ALBUM });
    const albumNotificationCounter = useRef(0);

    const container = useContainer();
    const { notify } = useNotify();
    const cookies = container.get(CONTAINER_COOKIE_STORAGE);
    let isWebNextNavigationOn = false;

    if (process.env.BUILD_TYPE === 'web') {
        isWebNextNavigationOn = cookies.get('WebNextNavigation') === 'on';
    }

    const { album, experiments } = useStore();
    const { formatMessage } = useIntl();

    const averageColor = useMemo(() => {
        return getAverageBackgroundColor(album?.meta?.averageColor);
    }, [album?.meta?.averageColor]);

    const { ref, offsetY } = useElementOffsetY();

    const headerStyle = useMemo(() => {
        if (offsetY !== undefined) {
            const initialTranslateHeader = isWebNextNavigationOn
                ? HEADER_TO_VIEWPORT_OFFSET_NEW_NAVIGATION
                : HEADER_TO_VIEWPORT_OFFSET_OLD_NAVIGATION;
            const translate = offsetY - initialTranslateHeader;
            return {
                '--average-color-background': averageColor,
                transform: `translateY(${offsetY >= initialTranslateHeader ? 0 : translate}px)`,
                opacity: 1
            };
        }
        return {};
    }, [offsetY, isWebNextNavigationOn, averageColor]);

    useEffect(() => {
        return () => {
            if (album.id !== albumId) {
                album.reset();
                albumNotificationCounter.current = 0;
            }
        };
    }, [album, albumId]);

    // TODO: существует до момента, пока не появится обработка ошибок моделей
    useEffect(() => {
        if (album.isAlbumLoadingError && !album.isNotFound) {
            if (albumNotificationCounter && albumNotificationCounter.current === 0) {
                notify(
                    <BaseNotificationError error={formatMessage({ id: 'album-errors.error-during-loading-album' })} />,
                    {
                        containerId: NotificationContainerId.ERROR
                    }
                );

                albumNotificationCounter.current++;
            }
        }

        if (
            album.otherArtistAlbums.loadingState === LoadingState.REJECT ||
            album.latestGenreAlbums.loadingState === LoadingState.REJECT
        ) {
            if (albumNotificationCounter && albumNotificationCounter.current === 0) {
                notify(
                    <BaseNotificationError
                        error={formatMessage({ id: 'album-errors.error-during-loading-similar-albums' })}
                    />,
                    { containerId: NotificationContainerId.ERROR }
                );

                albumNotificationCounter.current++;
            }
        }
    }, [
        album.isAlbumLoadingError,
        album.isNotFound,
        album.latestGenreAlbums.loadingState,
        album.otherArtistAlbums.loadingState,
        formatMessage,
        notify
    ]);

    const albumHeader = useMemo(() => {
        if (album.isAlbumLoading || album.isAlbumLoadingError) {
            return <PageHeaderShimmer className={styles.header} />;
        }
        return (
            <PageHeaderAlbum
                album={album.meta}
                className={styles.header}
                aria-labelledby="virtual-grid-header"
                ref={ref}
            />
        );
    }, [album.isAlbumLoading, album.isAlbumLoadingError, album.meta, ref]);

    const customComponents = useMemo(() => {
        return {
            Header: () => {
                return (
                    <>
                        <Header />
                        {albumHeader}
                    </>
                );
            },
            Footer: () => (
                <div className={styles.footerContainer}>
                    <div>
                        {album.isOtherAlbumVersionsExist && (
                            <AlbumsCarousel
                                className={styles.carousel}
                                containerClassName={clsx(styles.carouselContainer, styles.important)}
                                isLoading={album.isAlbumLoading}
                                title={formatMessage({ id: 'entity-names.other-album-versions' })}
                                albums={album.otherAlbumVersions}
                            />
                        )}
                        {album.isLatestGenreAlbumsExist && (
                            <AlbumsCarousel
                                className={styles.carousel}
                                containerClassName={clsx(styles.carouselContainer, styles.important)}
                                isLoading={album.isLatestGenreAlbumsLoading}
                                title={formatMessage({ id: 'entity-names.new-albums-in-genre' })}
                                albums={album.latestGenreAlbums.items}
                            />
                        )}
                        {album.isOtherArtistAlbumsExist && (
                            <AlbumsCarousel
                                className={styles.carousel}
                                containerClassName={clsx(styles.carouselContainer, styles.important)}
                                isLoading={album.isOtherArtistAlbumsLoading}
                                title={formatMessage({ id: 'entity-names.other-albums-of-artist' })}
                                albums={album.otherArtistAlbums.items}
                            />
                        )}
                    </div>
                    <Footer className={styles.footer} />
                </div>
            )
        };
    }, [
        albumHeader,
        album.isOtherAlbumVersionsExist,
        album.isAlbumLoading,
        album.otherAlbumVersions,
        album.isLatestGenreAlbumsExist,
        album.isLatestGenreAlbumsLoading,
        album.latestGenreAlbums.items,
        album.isOtherArtistAlbumsExist,
        album.isOtherArtistAlbumsLoading,
        album.otherArtistAlbums.items,
        formatMessage
    ]);

    const getAlbumTracksByRange = useCallback(
        (range: ListRange) => {
            const trackIds = [];
            for (let i = range.startIndex; i <= range.endIndex; i++) {
                const item = album.items[i] as VolumeItemModel;
                if (item?.type === VolumeType.TRACK && item?.loadingState === LoadingState.IDLE) {
                    trackIds.push(item.id);
                }
            }
            if (trackIds.length) {
                album.getTracks({
                    trackIds
                });
            }
        },
        [album]
    );

    if (album.loadingState === LoadingState.IDLE) {
        use(
            album.getData({
                albumId
            })
        );
    }

    useRumPage(album.loadingState === LoadingState.RESOLVE);

    if (album.isNotFound) {
        notFound();
    }

    const itemContentCallback = (index: number): ReactElement | undefined => {
        const item = album?.items?.[index] as VolumeItemModel;

        if (!item) {
            return <TrackShimmer className={styles.shimmerItem} variant={TrackVariant.ALBUM} />;
        }

        if (!item?.data) {
            switch (item?.type) {
                case VolumeType.TEXT:
                    return <TextShimmer className={styles.shimmerItem} />;

                case VolumeType.TRACK:
                    return <TrackShimmer className={styles.shimmerItem} variant={TrackVariant.ALBUM} />;
            }
        }

        switch (item?.type) {
            case VolumeType.TRACK:
                return (
                    <TrackAlbum
                        track={item.data}
                        position={item.position}
                        albumArtists={album?.meta?.artists}
                        playContextParams={{
                            contextData: {
                                type: YaMusicContexts.Album,
                                meta: {
                                    id: album.meta.id
                                },
                                from
                            },
                            queueParams: {
                                index: album.trackIndexInContext.get(item.id)
                            }
                        }}
                    />
                );

            case VolumeType.TEXT:
                return (
                    <div className={clsx(styles.shimmerItem, styles.textItem)}>
                        <Caption variant="span" type="entity" size="m" weight="medium" className={styles.text}>
                            <FormattedMessage id="entity-names.disk-number" values={{ number: item.data }} />
                        </Caption>
                    </div>
                );
        }
    };

    const isAlbumTracksDisabled = experiments.checkExperiment(Experiments.WebNextAlbumDisableTracks, 'on');

    const itemCount = isAlbumTracksDisabled ? 0 : album.items.length || DEFAULT_TRACK_SHIMMER_COUNT;

    return (
        <AnalyticsPageProvider pageId={AnalyticsPage.ALBUM}>
            <div className={styles.averageColorBackground} style={headerStyle as CSSProperties} />
            <VirtualScroll
                className={clsx(styles.root, styles.important)}
                listClassName={styles.content}
                customComponents={customComponents}
                totalCount={itemCount}
                // Для корректного обновления виртуального скролла
                // eslint-disable-next-line react/jsx-no-bind
                itemContentCallback={itemContentCallback}
                onGetDataByRange={getAlbumTracksByRange}
                debounceDurationInMs={DEBOUNCE_DURATION_MS}
                initialItemCount={itemCount}
            />
        </AnalyticsPageProvider>
    );
});
