import { forwardRef, ReactElement, Ref } from 'react';

import { WithClassName } from '@/shared/lib/index.js';
import { CarouselBlock } from '@/shared/ui/index.js';

import { AlbumWithArtistsModel } from '../../model/AlbumWithArtists.js';
import { AlbumCard } from '../AlbumCard/AlbumCard.js';

interface Props extends WithClassName {
    isLoading?: boolean;
    title: string;
    description?: string;
    albums?: AlbumWithArtistsModel[];
    containerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
    headerClassName?: string;
}

const AlbumsCarouselComponent = ({
    forwardRef: ref,
    title,
    description,
    albums,
    isLoading,
    className,
    containerClassName,
    headerClassName
}: Props): ReactElement | null => {
    return (
        <CarouselBlock
            className={className}
            headerClassName={headerClassName}
            containerClassName={containerClassName}
            isLoading={isLoading}
            ref={ref}
            title={title}
            description={description}
        >
            {albums?.map((album: AlbumWithArtistsModel) => {
                return <AlbumCard key={album.id} album={album} contentLinesCount={3} />;
            })}
        </CarouselBlock>
    );
};

export const AlbumsCarousel = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => {
    return <AlbumsCarouselComponent forwardRef={ref} {...props} />;
});
