import { Instance, types } from 'mobx-state-tree';

import { Album } from '@/entities/album/index.js';
import { Artist, ArtistModel } from '@/entities/artist/index.js';

export const AlbumWithArtists = Album.props({
    artists: types.maybe(types.array(Artist))
})
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get artistNames() {
            return self.artists?.map((artist: ArtistModel) => artist.name).join(', ');
        }
    }));

export type AlbumWithArtistsModel = Instance<typeof AlbumWithArtists>;
