'use client';

import { observer } from 'mobx-react-lite';
import { useEffect } from 'react';

import { NotFound } from '@/widgets/not-found/index.js';

import { useStore } from '@/shared/lib/index.js';

export const AlbumNotFoundPage = observer(() => {
    const { album } = useStore();

    useEffect(() => {
        return () => {
            album.reset();
        };
    }, [album]);

    return <NotFound />;
});
