'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import React, { MouseEvent, ReactElement, useCallback, useMemo, useState } from 'react';
import { useIntl } from 'react-intl';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { AlbumType, ContentWarning } from '@yandex-music-int/resource-collection';
import { Button } from '@yandex-music-int/sol/components/Button';
import { EntityCard, EntityCardProps, CommonCardControls } from '@yandex-music-int/sol/components/EntityCard';
import { ExplicitMarkVariant } from '@yandex-music-int/sol/components/ExplicitMark';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { useOnLikeClick } from '@/entities/album/index.js';
import { SeparatedArtists } from '@/entities/artist/index.js';

import {
    useSearchFeedback,
    WithClassName,
    WithChildren,
    usePlayableContext,
    additionFormatter,
    useCreateFrom
} from '@/shared/lib/index.js';
import { Link, PlayButton, LikeButton, useFuture } from '@/shared/ui/index.js';

import { AlbumWithArtistsModel } from '../../model/AlbumWithArtists.js';

import styles from './AlbumCard.module.scss';

export interface Props extends WithClassName, WithChildren {
    album: AlbumWithArtistsModel;
    contentLinesCount?: EntityCardProps['contentLinesCount'];
}

export const AlbumCard = observer(({ className, children, album, contentLinesCount }: Props): ReactElement => {
    const from = useCreateFrom();
    const { isFuture } = useFuture();
    const { formatMessage } = useIntl();
    const handleOnLikeClick = useOnLikeClick(album);
    const { sendLikeSearchFeedback, sendNavigateSearchFeedback, sendPlaySearchFeedback } = useSearchFeedback();

    const albumSingleType = album.type === AlbumType.SINGLE ? formatMessage({ id: 'entity-names.single' }) : undefined;
    const addition = additionFormatter([album.year, albumSingleType, album.version]);
    const explicitMark = album.contentWarning === ContentWarning.EXPLICIT ? ExplicitMarkVariant.E : null;
    const albumName = useMemo(() => {
        return formatMessage({ id: 'entity-names.album-name' }, { albumName: album.title });
    }, [album.title, formatMessage]);

    const { isPlaying, togglePlay } = usePlayableContext({
        playContextParams: {
            contextData: {
                type: YaMusicContexts.Album,
                meta: {
                    id: album.id
                },
                from
            },
            loadContextMeta: true
        }
    });

    //TODO: заменить на внутреннюю обработку в MenuButton
    const handleClick = useCallback((event: MouseEvent<HTMLButtonElement>) => {
        event.stopPropagation();
        event.preventDefault();
    }, []);

    const handleNavigate = useCallback(() => {
        sendNavigateSearchFeedback?.();
    }, [sendNavigateSearchFeedback]);

    const [onSearchFeedbackPlayed, setOnSearchFeedbackPlayed] = useState(false);
    const handlePlayButtonClick = useCallback(() => {
        if (!onSearchFeedbackPlayed && !isPlaying) {
            setOnSearchFeedbackPlayed(true);
            sendPlaySearchFeedback?.();
        }

        togglePlay();
    }, [isPlaying, onSearchFeedbackPlayed, sendPlaySearchFeedback, togglePlay]);

    const [onSearchFeedbackLiked, setOnSearchFeedbackLiked] = useState(false);
    const handleLikeButtonClick = useCallback(() => {
        if (!onSearchFeedbackLiked && !album.isLiked) {
            setOnSearchFeedbackLiked(true);
            sendLikeSearchFeedback?.();
        }

        handleOnLikeClick();
    }, [album.isLiked, handleOnLikeClick, onSearchFeedbackLiked, sendLikeSearchFeedback]);

    const menuControl = useMemo(() => {
        if (isFuture) {
            return (
                <Button
                    className={clsx(styles.icon, styles.control)}
                    //TODO: Заменить на релевантное имя, когда будет готово контекстное меню
                    aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                    icon={<Icon size="xxs" variant="more" />}
                    size="s"
                    radius="round"
                    onClick={handleClick}
                />
            );
        }

        return null;
    }, [isFuture, handleClick, formatMessage]);

    const view = useMemo(() => {
        return (
            <Paper className={styles.cover} radius="s" withShadow>
                <Link
                    className={styles.coverLink}
                    href={`/album/${album.id}`}
                    aria-label={album.title}
                    onClick={handleNavigate}
                >
                    <Image
                        className={styles.image}
                        src={album.coverUri}
                        size={200}
                        fit="cover"
                        alt={albumName}
                        withAvatarReplace
                    />
                    <CommonCardControls
                        className={styles.controls}
                        playControl={
                            <PlayButton
                                className={clsx(styles.playButton, styles.control)}
                                buttonVariant="default"
                                withHover={false}
                                iconSize="l"
                                variant="filled"
                                onClick={handlePlayButtonClick}
                                isPlaying={isPlaying}
                                disabled={!album.isAvailable}
                            />
                        }
                        likeControl={
                            <LikeButton
                                isLiked={album.isLiked}
                                onClick={handleLikeButtonClick}
                                variant="default"
                                size="s"
                                iconSize="xxs"
                                className={styles.control}
                            />
                        }
                        menuControl={menuControl}
                    />
                </Link>
            </Paper>
        );
    }, [
        album.id,
        album.title,
        album.coverUri,
        album.isAvailable,
        album.isLiked,
        handleNavigate,
        albumName,
        handlePlayButtonClick,
        isPlaying,
        handleLikeButtonClick,
        menuControl
    ]);

    return (
        <EntityCard
            className={clsx(styles.root, className)}
            aria-label={albumName}
            explicitMark={explicitMark}
            title={
                <Caption variant="div" type="entity" size="s" weight="medium" lineClamp={2}>
                    <Link className={styles.titleLink} href={`/album/${album.id}`} onClick={handleNavigate}>
                        {album.title}
                    </Link>
                </Caption>
            }
            contentLinesCount={contentLinesCount}
            view={view}
            description={
                <SeparatedArtists
                    className={styles.artists}
                    artists={album.artists}
                    lineClamp={1}
                    linkClassName={styles.artistLink}
                    captionSize="s"
                />
            }
        >
            {addition && (
                <Caption className={styles.addition} variant="div" type="entity" size="s" weight="medium" lineClamp={1}>
                    {addition}
                </Caption>
            )}
            {children}
        </EntityCard>
    );
});
