import { cast } from 'mobx-state-tree';

import { Album as AlbumResponse, Artist as ArtistResponse } from '@yandex-music-int/resource-collection';

import { albumResponseToModel } from '@/entities/album/index.js';
import { artistResponseToModel } from '@/entities/artist/index.js';

import { AlbumWithArtistsModel } from '../model/AlbumWithArtists.js';

export const albumWithArtistsResponseToModel = (response: AlbumResponse): AlbumWithArtistsModel => {
    const artists = response.artists.map((artist: ArtistResponse) => {
        return artistResponseToModel(artist);
    });

    return cast({
        ...albumResponseToModel(response),
        artists
    });
};
